.timeline-container {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 0;
}

.timeline-line {
  position: absolute;
  left: 20px;
  top: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(to bottom, #6e8efb, #a777e3);
  transform: translateX(-50%);
  z-index: 1;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.5s ease;
  z-index: 2;
}

.timeline-item.visible {
  opacity: 1;
  transform: translateY(0);
}

.timeline-dot {
  position: absolute;
  left: 20px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: white;
  border: 4px solid #6e8efb;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-dot {
  transform: translate(-50%, -50%) scale(1.2);
  box-shadow: 0 0 15px rgba(110, 142, 251, 0.5);
}

.timeline-icon {
  font-size: 14px;
}

.timeline-year {
  position: absolute;
  left: 0;
  width: 80px;
  text-align: right;
  padding-right: 20px;
  font-weight: bold;
  color: #6e8efb;
}

.timeline-content {
  margin-left: 100px;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-content,
.timeline-item.active .timeline-content {
  transform: translateX(10px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
}

.timeline-content h4 {
  margin-top: 0;
  color: #333;
}

.timeline-content p {
  margin-bottom: 0;
  color: #666;
}

.timeline-details {
  margin-top: 10px;
  animation: fadeIn 0.3s ease;
}

.timeline-button {
  background: linear-gradient(135deg, #6e8efb, #a777e3);
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.timeline-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 10px rgba(0,0,0,0.1);
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Responsive design */
@media (max-width: 768px) {
  .timeline-line {
    left: 30px;
  }
  
  .timeline-dot {
    left: 30px;
  }
  
  .timeline-year {
    width: 60px;
    padding-right: 10px;
    font-size: 14px;
  }
  
  .timeline-content {
    margin-left: 70px;
    padding: 15px;
  }
}